import $ from 'jquery';
import '../vendor/bootstrap/js/bootstrap.min';
import '../vendor/formValidator/js/formValidation.min';
import '../vendor/formValidator/js/bootstrap';
import '../vendor/formValidator/js/zh_CN';

$(function () {
    checkNow();
});

function checkNow() {
    function randomNumber(min, max) {
        return Math.floor(Math.random() * (max - min + 1) + min);
    }

    $('#codeLook').html([randomNumber(1, 100), '+', randomNumber(1, 200), '='].join(' '));

    $("#registerForm").formValidation({
        message: "",
        icon: {
            valid: 'glyphicon glyphicon-ok',
            invalid: 'glyphicon glyphicon-remove',
            validating: 'glyphicon glyphicon-refresh'
        },
        fields: {
            username: {
                selector: "#username",
                verbose: false,
                validators: {
                    notEmpty: {
                        message: ""
                    },
                    stringLength: {
                        min: 4,
                        max: 30,
                        message: "430"
                    },
                    regexp: {
                        regexp: /^[a-zA-Z0-9_]+$/,
                        message: ""
                    }
                }
            },
            mail: {
                selector: "#mail",
                validators: {
                    emailAddress: {
                        message: "email"
                    }
                }
            },

            password: {
                selector: "#password",
                validators: {
                    notEmpty: {
                        message: ""
                    },
                    different: {
                        field: "username",
                        message: ""
                    },
                    callback: {
                        message: "",
                        callback: function (value, validator, $field) {
                            if (value === '') {
                                return true;
                            }

                            if (value.length < 4) {
                                return {
                                    valid: false,
                                    message: "8"
                                };
                            }

                            if (value === value.toLowerCase()) {
                                return {
                                    valid: false,
                                    message: ''
                                }
                            }

                            if (value === value.toUpperCase()) {
                                return {
                                    valid: false,
                                    message: ''
                                }
                            }

                            if (value.search(/[0-9]/) < 0) {
                                return {
                                    valid: false,
                                    message: ''
                                }
                            }

                            return true;
                        }
                    }
                }
            },

            rePassword: {
                selector: "#rePassword",
                validators: {
                    notEmpty: {
                        message: ""
                    },
                    callback: {
                        message: "",
                        callback: function (value, validator, $field) {
                            if (value === '') {
                                return true;
                            }
                            if (value != $("#password").val()) {
                                return {
                                    valid: false,
                                    message: ""
                                };
                            }
                            return true;
                        }
                    }
                }
            },

            code: {
                selector: "#code",
                validators: {
                    callback: {
                        message: "",
                        callback: function (value, validator, $field) {
                            let items = $("#codeLook").html().split(' ');
                            let sum = parseInt(items[0]) + parseInt(items[2]);
                            return value == sum;
                        }
                    }
                }
            }
        },
        onSuccess:function(){
            $("#registerButton").on({
                click: function(){
                    $.post(
                        "user/register",
                        {
                            "function": "register",
                            "username": $("#username").val(),
                            "mail": $("#mail").val(),
                            "password": $("#password").val()
                        },
                        function(data){
                            if(data.flag){
                                alert("");
                                $("#register").data('formValidation').resetForm();
                            }else{
                                alert(data.errorMsg);
                            }
                        }
                    );
                }
            });
        }
    });
}



